<!--时间详情  模块-->
<template>
  <el-table
      :data="tableData"
      :row-style= 'rowstyle'
      :show-header="status"
      :cell-style= 'rowstyle'
      height="440"
      style="width: 100%; background-color: transparent !important;">
      <el-table-column
      label=""
      :cell-style= 'rowstyle'
      align='center'
        prop="dataname"
        width="120">
      </el-table-column>
      <el-table-column
        prop="datavalue"
        width="245"
        :show-overflow-tooltip="true">
      </el-table-column>
    </el-table>
</template>

<script>
export default {
  data () {
    return {
      tableData: [{
        dataname: '发生时间:',
        datavalue: ''
      }, {
        dataname: '发生地址:',
        datavalue: ''
      }, {
        dataname: '事件内容:',
        datavalue: ''
      }, {
        dataname: '事件类型:',
        datavalue: ''
      }, {
        dataname: '接收单位:',
        datavalue: ''
      }, {
        dataname: '事件来源:',
        datavalue: ''
      }, {
        dataname: '案件起因类型:',
        datavalue: ''
      }, {
        dataname: '损害程度:',
        datavalue: ''
      }, {
        dataname: '采取措施 :',
        datavalue: ''
      }],
      status: false
    }
  },
  props: ['eventInfo'],
  methods: {
    rowstyle ({row, rowIndex}) {
      return 'background-color: transparent; border: 0; color: #88d6ff; row:hover : black'
    },
    loadEvent () {
      this.tableData[0].datavalue = this.eventInfo.Eventtime // 发生时间
      this.tableData[1].datavalue = this.eventInfo.Address // 发生地址
      this.tableData[2].datavalue = this.eventInfo.Content // 事件内容
      this.tableData[3].datavalue = this.eventInfo.Ajlbname// 事件类型
      this.tableData[4].datavalue = this.eventInfo.Receiveunitname// 接收单位
      this.tableData[5].datavalue = this.eventInfo.Sourcetype// 事件来源
      this.tableData[6].datavalue = this.eventInfo.Causetype// 事件起因类型
      if (this.eventInfo.Degree === 'ordinary') {
        // 损害程度
        this.tableData[7].datavalue = '一般'
      }
      if (this.eventInfo.Degree === 'larger') {
        this.tableData[7].datavalue = '较大'
      }
      if (this.eventInfo.Degree === 'important') {
        this.tableData[7].datavalue = '重大'
      }
      // alert(this.eventInfo.Measure)
      this.tableData[8].datavalue = this.eventInfo.Measure.replace('\n', '') // 采取措施
    }
  },
  mounted () {
    // 根据传过来的event对象，绑定数据
    this.loadEvent()
  }
}
</script>

<style lang="stylus" >
el-table{
  background : transparent !important;
}
.el-table::before{
  height : 0;
}
</style>



